            >
              <b-input
                v-model.number="personAnswers.data.organisation"
                type="text"
                @blur="$v.personAnswers.data.organisation.$touch()"
              >
              </b-input>
            </b-field>

            <b-field
              label="What category best describes your organization's focus?"
              :type="{ 'is-danger': $v.personAnswers.data.industry.$error }"
            >
              <b-select
                v-model="personAnswers.data.industry"
