                  {{ industry }}
                </option>
              </b-select>
            </b-field>
          </div>
          <div class="column is-6">
            <b-field
              label="How satisfied are you with your current occupation?"
              :type="{
                'is-danger': $v.personAnswers.data.occupationSatisfaction.$error
              }"
              class="bottom-space"
              :message="
                $v.personAnswers.data.occupationSatisfaction.$error
                  ? 'Please choose how satisfied you are with your current occupation'
                  : ''
              "
            >
              <div class="level">
                <div class="level-item">
                  Extremely <br />
                  Dissatisfied
                </div>
                <div class="level-item">
                  <b-radio-button
                    v-model="personAnswers.data.occupationSatisfaction"
