                    :key="level"
                    :native-value="level"
                  >
                    {{ level }}
                  </b-radio-button>
                </div>
                <div class="level-item">
                  Extremely <br />
                  Satisfied
                </div>
              </div>
            </b-field>

            <b-field
              label="Do you have children?"
              :type="{ 'is-danger': $v.personAnswers.data.children.$error }"
              :class="{ 'error-field': $v.personAnswers.data.children.$error }"
              class="bottom-space"
            >
              <div class="block">
                <b-radio
                  v-model="personAnswers.data.children"
                  native-value="true"
                  @blur="$v.personAnswers.data.children.$touch()"
                >
                  Yes
                </b-radio>
                <b-radio
                  v-model="personAnswers.data.children"
                  native-value="false"
                  @blur="$v.personAnswers.data.children.$touch()"
                >
                  No
                </b-radio>
              </div>
            </b-field>

            <b-field
              label="What is your current living situation?"
              :type="{
                'is-danger': $v.personAnswers.data.livingSituation.$error
              }"
              :class="{
                'error-field': $v.personAnswers.data.livingSituation.$error
              }"
              class="bottom-space"
            >
              <div class="block">
                <b-radio
                  v-model="personAnswers.data.livingSituation"
                  v-for="situation in livingSituations"
                  :key="situation"
                  :native-value="situation"
                  @blur="$v.personAnswers.data.livingSituation.$touch()"
                >
                  {{ situation }}
                </b-radio>
              </div>
            </b-field>

            <b-field
              label="How satisfied are you with your current living situation?"
              :type="{
                'is-danger':
                  $v.personAnswers.data.livingSituationSatisfaction.$error
              }"
              class="bottom-space"
              :message="
                $v.personAnswers.data.livingSituationSatisfaction.$error
                  ? 'Please choose how satisfied you are with your current living situation'
                  : ''
              "
            >
              <div class="level">
                <div class="level-item">
                  Extremely <br />
                  Dissatisfied
                </div>
                <div class="level-item">
                  <b-radio-button
