          . . . and who you are
        </h2>
        <div class="columns">
          <div class="column is-6">
            <b-field
              label="Have you been involved in the starting of a new Venture"
            >
              <b-input
                v-model.number="personAnswers.data.founded"
                type="text"
                placeholder="E.g. Non-Profit, Company or Org"
              >
              </b-input>
            </b-field>

            <b-field
              label="Highest Level of Education"
              :type="{
                'is-danger': $v.personAnswers.data.educationLevel.$error
              }"
            >
              <b-select
                v-model="personAnswers.data.educationLevel"
                placeholder="Select One"
                @blur="$v.personAnswers.data.educationLevel.$touch()"
                expanded
              >
                <option selected disabled>Choose One</option>
                <option
                  v-for="option in educationLevels"
                  :value="option"
                  :key="option"
                >
                  {{ option }}
                </option>
              </b-select>
            </b-field>
          </div>
        </div>

        <div class="columns">
          <div class="column">
            <legend class="label">
              Have you attended TEDxAuckland before?
            </legend>
            <div class="checkbox-row">
              <b-checkbox
                v-for="event in previousEvents"
                :key="event"
                class="checkbox-column"
                v-model="personAnswers.data.eventsAttended"
                :native-value="event"
              >
                {{ event }}
              </b-checkbox>
            </div>
          </div>
        </div>

        <div class="columns is-mobile">
          <div class="column">
            <survey-nav :id="$route.params.id" :active="2" />
          </div>

          <div class="column">
            <div class="buttons has-addons is-right">
              <div class="button is-medium" @click="previousPage">
                Previous
              </div>
              <div
                class="button is-primary is-medium"
                @click.prevent="finish()"
              >
                Accept and Finish
              </div>
            </div>
          </div>
