  </div>
</template>

<script>
import NProgress from "nprogress";
import { required } from "vuelidate/lib/validators";
import { personNameMixin } from "@/mixins/personNameMixin";
import SurveyNav from "@/community/components/SurveyNav";

import ProfileService from "@/services/ProfileService";
export default {
  name: "SurveyPageTwo",
  components: { SurveyNav },
  data() {
    return {
      personAnswers: this.person
    };
  },
  props: {
    person: {
      type: Object,
      required: true
    }
  },
  mixins: [personNameMixin],
  validations: {
    personAnswers: {
      data: {
        occupation: { required },
        occupationSatisfaction: { required },
        organisation: { required },
        industry: { required },
        educationLevel: { required },

        children: { required },
        livingSituation: { required },
        livingSituationSatisfaction: { required }
      }
    }
  },
  computed: {
    previousEvents() {
      return this.$store.state.previousEvents;
    },
    formattedAddress() {
      if (this.personAnswers.data.address) {
        const address = this.personAnswers.data.address;
        return `${address.streetNumber} ${address.route}, ${
          address.locality
        }, ${address.administrativeAreaLevel1}, ${address.postalCode}`;
      } else {
        return "";
      }
    },
    industries() {
      return this.$store.state.industries;
    },
    livingSituations() {
      return this.$store.state.livingSituations;
    },
    satisfactionLevels() {
      return this.$store.state.satisfactionLevels;
    },
    educationLevels() {
      return this.$store.state.educationLevels;
    }
  },
  methods: {
    // nextPage() {
    //   this.$v.$touch();
    //   if (!this.$v.$invalid) {
    //     NProgress.start();
    //     ProfileService.updateProfile(this.person.id, {
    //       profile: this.person.data
    //     })
    //       .then(() => {
    //         this.$router.push({
    //           name: "survey-three",
    //           params: { id: this.person.id }
    //         });
    //         NProgress.done();
    //       })
    //       .catch(e => {
    //         NProgress.done();
    //         console.log("ERROR", e);
    //       });
    //   }
    // },
    finish() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        ProfileService.updateProfile(this.person.id, {
          profile: {
            ...this.person.data,
            surveyComplete: true
          }
        })
          .then(() => {
            this.$router.push({
              name: "survey-finish",
              params: { id: this.person.id }
            });
            NProgress.done();
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    },
    previousPage() {
      NProgress.start();
      ProfileService.updateProfile(this.person.id, {
        profile: this.person.data
      })
        .then(() => {
          this.$router.push({
            name: "survey-one",
            params: { id: this.person.id }
          });
          NProgress.done();
        })
        .catch(e => {
          NProgress.done();
          console.log("ERROR", e);
        });
    },
    getAddressData(data) {
      this.personAnswers.data.address = {
        streetNumber: data.street_number,
        route: data.route,
        locality: data.locality,
        administrativeAreaLevel1: data.administrative_area_level_1,
        country: data.country,
        postalCode: data.postal_code,
        latitude: data.latitude,
        longitude: data.longitude
      };
    }
  }
};
</script>

<style>
.bottom-space {
  margin-bottom: 1.5rem !important;
}
