<template>
  <div>
    <section class="container section">
      <div class="columns">
        <div class="column">
          <h1 class="title has-text-poppins has-weight-light is-1">
            TEDxAuckland Co-Lab
          </h1>
        </div>
      </div>

      <h2 class="title has-text-poppins is-5 has-red-stripe">
        last question!
      </h2>
      <div class="columns">
        <div class="column is-7">
          <div class="content">
            <p>
              We also wanted to invite you to the
              <strong>TEDxAuckland Co-Lab</strong>. It's a place where youll
              get a first look at emerging ideas, technology, products and
              services from us and our partners.
            </p>

            <p>
              We'd love for you to be involved. Please tick the box below if
              this sounds like something you'd be interested in.
            </p>
          </div>
        </div>
        <div class="column is-5">
          <img
            src="/assets/ticketing-app/images/photo-of-dinner.jpg"
            alt="Photograph of the TEDxAuckland Community"
          />
        </div>
      </div>

      <div class="title has-text-poppins is-5 has-other-stripe"></div>
      <div class="columns">
        <form @submit.prevent>
          <div class="column">
            <div class="section">
              <div class="field">
                <b-checkbox
                  size="is-large"
                  v-model="personAnswers.data.betaGroupsOptIn"
                >
                  <span class="has-text-poppins has-weight-light">
                    I'm in, I want to be part of awesome!
                  </span>
                </b-checkbox>
              </div>
            </div>
          </div>
        </form>
      </div>
      <div class="title has-text-poppins is-5 has-red-stripe"></div>

      <div class="columns is-mobile">
        <div class="column">
          <survey-nav :id="$route.params.id" :active="3" />
        </div>

        <div class="column">
          <div class="buttons has-addons is-right">
            <div class="button is-medium" @click="previousPage">
              Previous
            </div>
            <div
              class="button is-primary is-medium"
              @click.prevent="checkOptIn"
            >
              Finish
            </div>
          </div>
        </div>
      </div>
    </section>

    <b-modal :active.sync="isConfirmOptOutActive">
      <div class="modal-card center-modal">
        <header class="modal-card-head">
          <p class="modal-card-title">Are you sure?</p>
          <button
            class="delete"
            @click.prevent="isConfirmOptOutActive = false"
            aria-label="close"
          ></button>
        </header>
        <section class="modal-card-body">
          <p>
            Are you sure you don't want to be a member of TEDxAuckland Co-Lab?
          </p>
          <p>
            You'll get cool opportunites to be the first to see and shape new
            products, services and technology.
          </p>
        </section>
        <footer class="modal-card-foot">
          <button @click.prevent="optInAndFinish" class="button is-primary">
            Yes, Count me in
          </button>
          <button @click.prevent="finish" class="button">
            No thank you
          </button>
        </footer>
      </div>
    </b-modal>
  </div>
</template>

<script>
import NProgress from "nprogress";
import { required } from "vuelidate/lib/validators";
import { personNameMixin } from "@/mixins/personNameMixin";
import SurveyNav from "@/community/components/SurveyNav";

import ProfileService from "@/services/ProfileService";
export default {
  name: "SurveyPageFour",
  components: { SurveyNav },
  data() {
    return {
      personAnswers: this.person,
      isConfirmOptOutActive: false
    };
  },
  props: {
    person: {
      type: Object,
      required: true
    }
  },
  mixins: [personNameMixin],
  validations: {
    personAnswers: {
      data: {
        betaGroupsOptIn: { required }
      }
    }
  },
  computed: {},
  methods: {
    checkOptIn() {
      if (this.personAnswers.data.betaGroupsOptIn == false) {
        this.isConfirmOptOutActive = true;
      } else {
        this.finish();
      }
    },
    optInAndFinish() {
      this.personAnswers.data.betaGroupsOptIn = true;
      this.finish();
    },
    finish() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        ProfileService.updateProfile(this.person.id, {
          profile: {
            ...this.person.data,
            surveyComplete: true
          }
        })
          .then(() => {
            this.$router.push({
              name: "survey-finish",
              params: { id: this.person.id }
            });
            NProgress.done();
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    },
    previousPage() {
      NProgress.start();
      ProfileService.updateProfile(this.person.id, {
        profile: this.person.data
      })
        .then(() => {
          this.$router.push({
            name: "survey-two",
            params: { id: this.person.id }
          });
          NProgress.done();
        })
        .catch(e => {
          NProgress.done();
          console.log("ERROR", e);
        });
    },
    getAddressData(data) {
      this.personAnswers.data.address = {
        streetNumber: data.street_number,
        route: data.route,
        locality: data.locality,
        administrativeAreaLevel1: data.administrative_area_level_1,
        country: data.country,
        postalCode: data.postal_code,
        latitude: data.latitude,
        longitude: data.longitude
      };
    }
  }
};
</script>

