<template>
  <div>
    <section class="container section">
      <div class="columns">
        <div class="column is-8">
          <h1 class="title has-text-poppins has-weight-light is-1">
            {{ personName }}
          </h1>
        </div>
      </div>

      <h2
        v-if="person.unsubscribed"
        class="title has-text-poppins is-5 has-red-stripe"
      >
        You are currently Unsubscribed
      </h2>
      <h2 v-else class="title has-text-poppins is-5 has-red-stripe">
        You are currently Subscribed
      </h2>
      <div class="columns">
        <div class="column is-6">
          <div v-if="person.unsubscribed" class="content">
            <p></p>
          </div>
          <div v-else class="content">
            <p>
              Want to change how you receive these emails?
            </p>
            <p>
              You can unsubscribe from this list.
            </p>
          </div>
          <div
            v-if="personAnswers.unsubscribed"
            class="button is-primary is-large"
            @click.prevent="resubscribe"
          >
            Resubscribe
          </div>
          <div
            v-else
            class="button is-primary is-large"
            @click.prevent="unsubscribe"
          >
            Unsubscribe
          </div>
        </div>
        <div class="column is-6">
          <img
            src="/assets/ticketing-app/images/photo-of-crowd.jpg"
            alt="Photograph of the TEDxAuckland Community"
          />
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";

import ProfileService from "@/services/ProfileService";
export default {
  name: "Unsubscribe",
  data() {
    return {
      personAnswers: this.person
    };
  },
  props: {
    person: {
      type: Object,
      required: true
    }
  },
  mixins: [personNameMixin],
  methods: {
    unsubscribe() {
      NProgress.start();
      ProfileService.updateProfile(this.person.id, {
        person: {
          unsubscribed: true
        }
      })
        .then(() => {
          this.$snackbar.open(`Successfully unsubscribed`);
          this.$router.go();
          NProgress.done();
        })
        .catch(e => {
          NProgress.done();
          console.log("ERROR", e);
        });
    },
    resubscribe() {
      NProgress.start();
      ProfileService.updateProfile(this.person.id, {
        person: {
          unsubscribed: false
        }
      })
        .then(() => {
          this.$snackbar.open(`Successfully resubscribed`);
          this.$router.go();
          NProgress.done();
        })
        .catch(e => {
          NProgress.done();
          console.log("ERROR", e);
        });
    }
  }
};
</script>

<style></style>
